import React from 'react';
import Fragment from "../../../hoc/Fragment"


// import "../assets/css/dashboard.css"
// import "../assets/css/form.css"

import {connect} from 'react-redux'
import { 
  withRouter,
 } from "react-router-dom"
import {updateImage} from "../../../action/campaignActions"
import store from "../../../store";
import ImageUploader from 'react-images-upload';


class  EditImages extends React.Component{

// componentDidMount(){
//   const {user}= this.props.auth

//     console.log("USER", this.props.auth)
// }
    state = {
      image_slide_1:[],
      image_slide_2:[],
      image_slide_3:[],
    }
    
    componentDidMount(){
        console.log("IN THE PAGE RN")
        console.log(this.props)
        if(this.props.match.params.id){
           console.log(this.props.match.params.id)
        }
     }



   // IMAGE SLIDE 1 
   onDrop1 = (picture) =>{
    this.setState({
      image_slide_1: this.state.image_slide_1.concat(picture),
    })
}

// IMAGE SLIDE 2
onDrop2 = (picture) =>{
  this.setState({
    image_slide_2: this.state.image_slide_2.concat(picture),
  })
}


// IMAGE SLIDE 3
onDrop3 = (picture) =>{
this.setState({
  image_slide_3: this.state.image_slide_3.concat(picture),
})
}



     handleSubmit= async e => {
      e.preventDefault()      
      const fd= new FormData()
      const {user} =this.props.auth
      fd.append("userID",user.id)
      fd.append("image_slide_1",this.state.image_slide_1[0])
      fd.append("image_slide_2",this.state.image_slide_2[0])
      fd.append("image_slide_3",this.state.image_slide_3[0])
            const id = this.props.location.state.id
      await this.props.updateImage(fd, id)      
    }
    
    render(){
        return(
            <Fragment>
          <div class="card shadow mb-4">
            <div class="card-header py-3">
              <h6 class="m-0 font-weight-bold text-primary">EDIT CAMPAIGN IMAGES</h6>
            </div>
            <div class="card-body">
            {this.props.error.status === 400 ? 
                                   <div class="alert alert-warning alert-dismissible fade show" role="alert">
                                   <strong>
                                              {this.props.error.msg.msg} !!                        
                                   </strong> 
                                   <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                                     <span aria-hidden="true">&times;</span>
                                   </button>
                                 </div>
                           
                           
              : null}
            <form action="" onSubmit={this.handleSubmit} enctype="multipart/form-data">
    {/* PROJECT NAME */}


<div className="row mt-4">


<div className="col-md-6  mb-3">
<label for="text">UPLOAD AN IMAGE: JPG OR PNG</label>

<ImageUploader
                withIcon={true}
                buttonText='Choose images'
                onChange={this.onDrop1}
                imgExtension={['.jpg', '.gif', '.png', '.gif', '.jpeg']}
                withPreview = {true}
                maxFileSize={5242880}

                singleImage = {true}
                withPreview = {true}
            />


</div>


<div className="col-md-6">
  <label for="text">UPLOAD ANOTHER IMAGE: JPG OR PNG</label>
  <ImageUploader
                withIcon={true}
                buttonText='Choose images'
                onChange={this.onDrop2}
                imgExtension={['.jpg', '.gif', '.png', '.gif', '.jpeg']}
                withPreview = {true}
                maxFileSize={5242880}

                singleImage = {true}
                withPreview = {true}
            />
  </div>
</div>    



<div className="row mt-4">

<div className="col-md-6 mb-4">
  <label for="text">UPLOAD ANOTHER IMAGE: JPG OR PNG </label>
  <ImageUploader
                withIcon={true}
                buttonText='Choose images'
                onChange={this.onDrop3}
                imgExtension={['.jpg', '.gif', '.png', '.gif', '.jpeg']}
                withPreview = {true}
                maxFileSize={5242880}

                singleImage = {true}
                withPreview = {true}
            />
  </div>
</div>


  <hr className="hr" />
  <input className="btn btn-primary  btn-block" type="submit" value="submit" />
</form>

            </div>
          </div>


            </Fragment>
        )
    
    }
}


const mapStateToProps = state => ({
  auth: state.auth,
  error: state.error,
  campaign:state.campaign
});

// export default ProjectForm
export default connect(mapStateToProps,{
  updateImage 
})(
  withRouter(EditImages)
);