import axios from 'axios';
import { returnErrors } from './errorActions';
import {NOTIFICATION_LOADED} from './types'
var location = window.location.href
if(location.slice(0,17)== 'http://localhost:'){
  axios.defaults.baseURL = 'https://elesarrdevelopment.herokuapp.com';
  // axios.defaults.baseURL = 'http://localhost:5200';
}else{
  axios.defaults.baseURL = 'https://eles-api.herokuapp.com'
}


export const getNotifications = (ownerID) => dispatch => {
    // dispatch(setCampaignsLoading());
    axios
      .get(`/api/notifications/${ownerID}`)
      .then(res =>{

        // console.log("notifications ", res.data)
        dispatch({
          type: NOTIFICATION_LOADED,
          payload: res.data
        })
      }
      
      )
      // .then(
      //   dispatch(setUsersDoneLoading())
      // )
  
      .catch(err =>
        dispatch(returnErrors(err.response.data, err.response.status))
      );
  };
  
